* {
  padding: 0;
  margin: 0;
  outline: none;
}
ul {
  list-style-type: none;
}
.react-datepicker__input-container input{
  border: none; /* <-- This thing here */
  border: 1px solid #3F51B5 !important;
box-sizing: border-box;
border-radius: 2px;
height: 40px !important;
padding-left:48px;
